      // CASHCOUNTER("现金在柜台汇款(Over the counter cash remittance)"),
      // PHONEPAYMENT("电话转账(phone money transfer)"),
      // CROSS("跨行转账(Cross)"),
      // ALIPAYDIRECT("支付宝(Alipay)"),
      // OTHERS("OTHERS")*/
