(function () {
  'use strict';

  angular
    .module('pmsJs')
    .service('user', user);

  /** @ngInject */
  function user($window, base, $rootScope) {
    // "create",
    // "get",
    // "update",
    // "captcha",
    // "login",
    // "logout",
    // "isLogin",
    // "getSMSCode",
    // "updatePaymentInfo",
    // "updatePassword",
    // "updateSMSSetting",
    // "isValidUsername",
    // "authenticate"
    angular.extend(user.prototype, base);
    var vm = this;
    vm.serviceName = 'user';
    vm.login = login;
    function login(item) {
      vm.emit('login', {
        item: {
          loginName: item.loginName,
          password: item.password,
          operatorName: item.operatorName
        }
      })
    }

    vm.creatuser = createUser;
    function createUser(item) {
      vm.emit('createUser', {
        item: item
      })
    }

    vm.queryUser = queryUser;
    function queryUser(item, page, size) {
      vm.emit('queryUser', {
        item: item,
        page: page,
        size: size
      })
    }

    vm.queryOperator = function (item) {
      vm.emit('queryOperator', {
        item: item
      })
    }
    vm.addOperator = function (item) {
      vm.emit('addOperator', {
        item: item
      })
    }
    vm.updateOperator = function (item) {
      vm.emit('updateOperator', {
        item: item
      })
    }
    vm.createUser = createUser;
    function createUser(item) {
      $rootScope.loading = true;
      vm.emit('createUser', {
        item: item
      })
    }

    vm.updateUserPassword = updateUserPassword;
    function updateUserPassword(item) {
      vm.emit('updateUserPassword', {
        item: item
      })
    }
    vm.updateOperatorPassword = function updateOperatorPassword(item){
      vm.emit('updateOperatorPassword', {
        item: item
      })
    }
    vm.updateUser = updateUser;
    function updateUser(item) {
      vm.emit('updateUser', {
        //item:item
        item: {
          id: item.id,
          password: item.password,
          function: item.function,
          userName: item.username,
          nickName: item.nickname,
          roles: item.roles,
          platformId: item.platformId
          //departmentId:item.departmentId
        }
      })
    }
  }
})();

(function() {
    'use strict';

    angular
        .module('pmsJs')
        .directive('uploadFile', uploadFile);

    /** @ngInject */
    function uploadFile() {
        var directive = {
            restrict: 'EA',
            templateUrl: 'app/components/uploadFile/uploadFile.html',
            scope: {
                ngModel: '=',
                ngDisabled: '=',
                name: '='
            },
            controller: UploadFileController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function UploadFileController($log, $timeout, Upload, uploadFileUrl, imgUrl) {
            var vm = this;

            vm.ngModel;
            vm.ngDisabled;
            vm.name;
            vm.loading = false;
            vm.uploaded = false;

            function uploadFiles(file, errFiles) {
                vm.loading = true;
                vm.f = file;
                vm.errFile = errFiles && errFiles[0];
                if (file) {
                    file.upload = Upload.upload({
                        url: uploadFileUrl,
                        data: {
                            img: file,
                            id: new Date().getTime()
                        }
                    });

                    file.upload.then(function(response) {
                        $timeout(function() {
                            file.result = response.data;
                            if (response.data.data.imgUrl) {
                                vm.ngModel = imgUrl + response.data.data.imgUrl;
                                vm.uploaded = true;
                                vm.loading = false;
                            }
                        });
                    }, function(response) {
                        if (response.status > 0)
                            vm.errorMsg = response.status + ': ' + response.data;

                    }, function(evt) {
                        file.progress = Math.min(100, parseInt(100.0 *
                            evt.loaded / evt.total));
                    });
                }
            }
            vm.uploadFiles = uploadFiles;
        }
    }

})();

