      var role = [];
      if (angular.isUndefined(vm.departmentitem.numbers)) {
        alertify.error('先选择部门')
        return;
      }
      for (var i = 0; i < vm.rolelist.length; i++) {
        if (vm.userRole[i])
          role.push(vm.rolelist[i].id);
      }
