      for (var i = 0; i < data.data.length; i++) {
        vm.recrodList[i].createTime = $filter('dateFormate')(vm.recrodList[i].createTime);
      }
      vm.total = data.totalnumber;
      vm.totalSum = data.totalamount
      vm.nowSum = data.pageamount
      $scope.$digest();
